import './styles/_recommended-products.scss';
import Swiper from 'swiper';
import { fetchRebuyRecommendedProducts, fetchProductsById, restructureRestProducts } from '../services/products';
import 'swiper/swiper-bundle.css';

import {
  getProductItemBody,
  attachProductItemEvents,
  renderVideos,
} from '../common/product-item';
import { createLazyAction } from '../utils/lazy-action';
import { renderPlaceholderContainers } from '../common/placeholder-loader';
import { waitObjectProperty } from '../utils/performance';
import { onDocumentReady } from '../utils/dom';

// Swiper.use([Navigation]);

const sliderOptions = {
  slidesPerView: 2.15,
  freeMode: true,
  spaceBetween: 5,
  breakpoints: {
    851: {
      slidesPerView: 4.15,
      freeMode: false,
    },
  },
};

const state = {
  recommendedProducts: [],
  renderedRecommendedItems: {},
  renderedStyledWithItems: {},
  renderedRecentItems: {},
  settings: {},
  elements: {},
  recommendationsSlider: null,
  viewedProducts: [],
  viewedProductsSlider: null,
  styledWithSlider: null,
  viewedProductIds: [],
  currentProductId: null,
};

const init = (showViewedProducts, showRecommendedProducts, showStyledWithProducts) => (
  saveViewedProduct(),
  cacheElements(),
  renderPlaceholderContainers(state.elements.viewedProductsLoader, 4, 'product'),
  renderPlaceholderContainers(state.elements.recommendationsLoader, 4, 'product'),
  renderPlaceholderContainers(state.elements.styledWithProductsLoader, 4, 'product'),
  showViewedProducts && applyViewedProductsList()(),
  hideViewedProducts(),
  showRecommendedProducts && applyRecommendedProductsList()(),
  showStyledWithProducts && applyStyledWithProductsList()()
);

const cacheElements = () => {
  state.settings = window.recommendedProductsSettings || {};
  state.settings.styledWithProducts = window?.productMetafields?.styledWith
  const recommendationsSection = document.querySelector('.recommendations--main');
  const viewedProductsSection = document.querySelector('.recommendations--recent');
  const styledWithProductsSection = document.querySelector('.recommendations--styled')
  state.elements = {
    recommendationsSection,
    viewedProductsSection,
    styledWithProductsSection,
    recommendationsLoader: recommendationsSection?.querySelector('.recommendations__loader'),
    viewedProductsLoader: viewedProductsSection?.querySelector('.recommendations__loader'),
    styledWithProductsLoader: styledWithProductsSection?.querySelector('.recommendations__loader')
  };
};

const hideViewedProducts = (forceHide) => {
  if (!window.viewedProductsSettings) return
  if (state.viewedProductIds.length && !forceHide) return;
  state.elements.viewedProductsSection.style.display = 'none';
};

const applyRecommendedProductsList = () => (
  createLazyAction(
    state.elements.recommendationsSection,
    async () => (
      await getRecommendedProducts(),
      initRecommendationsSlider(),
      await renderProducts(
        state.recommendedProducts,
        state.renderedRecommendedItems,
        state.elements.recommendationsSection,
        state.recommendationsSlider,
        true,
      )
    ),
    '.recommendations__loader',
  )
);

const applyStyledWithProductsList = () => {
  return (
    createLazyAction(
      state.elements.styledWithProductsSection,
      async () => (
        getStyledWithProducts(),
        initStyledWithSlider(),
        await renderProducts(
          state.styledWithProducts,
          state.renderedStyledWithItems,
          state.elements.styledWithProductsSection,
          state.styledWithSlider,
          false
        )
      ),
      '.recommendations__loader',  
    )
  )
}

const getViewedProducts = async () => {
  if (!window.viewedProductsSettings) return
  try {
    const savedProductIds = state.viewedProductIds.filter(({ id }) => (
      +id !== +state.currentProductId
    ));
    if (!savedProductIds.length) {
      hideViewedProducts(true);
      return;
    }
    state.viewedProducts = await fetchProductsById(savedProductIds);
    hideViewedProducts(!state.viewedProducts.length);
  } catch (er) {
    console.log(er);
    hideViewedProducts(true);
  }
};

const saveViewedProduct = () => {
  if (!window.viewedProductsSettings) return
  state.viewedProductIds = JSON.parse(localStorage.getItem('viewed-products') || '[]');

  if (document.body.dataset.templateName !== 'product') return;

  state.currentProductId = document.querySelector('.Product').dataset.productId;

  const isCurrentProductAlreadyViewed = state.viewedProductIds.findIndex(({ id }) => (
    id === state.currentProductId?.toString()
  )) > -1;

  if (isCurrentProductAlreadyViewed) return;

  const newProductIdsList = [...state.viewedProductIds];
  const { max_products_saved } = window.viewedProductsSettings;

  if (state.viewedProductIds.length === max_products_saved) {
    newProductIdsList.pop();
  }

  newProductIdsList.unshift({ id: state.currentProductId });

  localStorage.setItem('viewed-products', JSON.stringify(newProductIdsList));
};

const applyViewedProductsList = () => (
  createLazyAction(
    state.elements.viewedProductsSection,
    async () => (
      await getViewedProducts(),
      initViewedProductsSlider(),
      await renderProducts(
        state.viewedProducts,
        state.renderedRecentItems,
        state.elements.viewedProductsSection,
        state.viewedProductsSlider,
      )
    ),
    '.recommendations__loader',
  )
);

const initRecommendationsSlider = () => {
  state.recommendationsSlider = new Swiper(
    '.recommendations--main .recommendations__slider',
    {
      ...sliderOptions,
    },
  );
};

const initStyledWithSlider = () => {
  state.styledWithSlider = new Swiper(
    '.recommendations--styled .recommendations__slider',
    {
      ...sliderOptions,
    },
  );
};

const initViewedProductsSlider = () => {
  state.viewedProductsSlider = new Swiper(
    '.recommendations--recent .recommendations__slider',
    {
      ...sliderOptions,
    },
  );
};

const hideRecommendedProducts = () => {
  state.elements.recommendationsSection.classList.add('hidden');
};

const getRecommendedProducts = async () => {
  try {
    const { max_products_recommended } = window.recommendedProductsSettings;
    state.recommendedProducts = await fetchRebuyRecommendedProducts(
      [window.products[0].id],
      max_products_recommended,
    );
    if (!state.recommendedProducts.length) hideRecommendedProducts()
  } catch (er) {
    console.log(er);
    hideRecommendedProducts();
  }
};

const getStyledWithProducts = () => {
  if (state.settings.styledWithProducts) {
    state.styledWithProducts = restructureRestProducts(state.settings.styledWithProducts)
  } else {
    state.styledWithProducts = null;
    state.elements.styledWithProductsSection.classList.add('hidden')
  }
}

const renderProducts = async (
  products,
  renderedState,
  itemsContainer,
  slider,
  isRecommendedProdsSection,
) => {
  if (!products?.length) return;
  await waitObjectProperty("GLBE_PARAMS", window)
  if (GLBE_PARAMS.currencyCode !== "USD") {
    await waitObjectProperty("PriceInfo", window.GLBE_PARAMS)
  }  
  const productTemplates = products.map((product, index) => {
    const newProduct = {...product};
    if (GLBE_PARAMS.currencyCode !== "USD") {
      if (newProduct.compareAtPrice) {
        newProduct.compareAtPrice = glbeApp.modules.priceInfo.convertPrice(newProduct.compareAtPrice)
      }
      newProduct.price = glbeApp.modules.priceInfo.convertPrice(product.price)
    }
    const template = getProductItemBody(
      newProduct,
      index,
      renderedState,
      false,
      itemsContainer,
    );
    return template.replace('"product-item ', '"product-item swiper-slide ');
  });
  slider.appendSlide(productTemplates);
  attachProductItemEvents(
    renderedState,
    itemsContainer,
    isRecommendedProdsSection,
  );
  await renderVideos(products, renderedState);
  slider.update();
};

onDocumentReady(() => init(false, true, false));